const prompt = require('@system.prompt')

/**
 * ÊãºÊé• url ÂíåÂèÇÊï∞
 */
function queryString(url, query) {
  let str = []
  for (let key in query) {
    str.push(key + '=' + query[key])
  }
  let paramStr = str.join('&')
  return paramStr ? `${url}?${paramStr}` : url
}

function showToast(message = '', duration = 0) {
  if (!message) return
  prompt.showToast({
    message: message,
    duration
  })
}

export default {
  showToast,
  queryString
}
